.timer {
  position: absolute;
  top: 30px;
  left: 40px;
  width: 60px;
  height: 55px;
  border-radius: 2px;
  padding-top: 5px;
  border: solid white 3px;
  -webkit-animation: Timer-spin infinite 4s linear;
          animation: Timer-spin infinite 4s linear;
  font-size: 45px;
}

@-webkit-keyframes Timer-spin {
  0% { -webkit-transform: rotate(-25deg); transform: rotate(-25deg); }
  25% { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  50% { -webkit-transform: rotate(25deg); transform: rotate(25deg); }
  75% { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  100% { -webkit-transform: rotate(-25deg); transform: rotate(-25deg); }
}

@keyframes Timer-spin {
  0% { -webkit-transform: rotate(-25deg); transform: rotate(-25deg); }
  25% { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  50% { -webkit-transform: rotate(25deg); transform: rotate(25deg); }
  75% { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  100% { -webkit-transform: rotate(-25deg); transform: rotate(-25deg); }
}


